	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"May Day","date":"01 May 2000","slug":"/2000-05-01-may-day/","tags":"Y2000"},"html":"<p>We discovered a new walking trail today... a secret path through a cow pasture to a foot bridge connecting Thelstorf and Weihe across the Seeve creek. The 90 minute round is almost all paved, good for pushing the baby carriage! Of course we did not have the camera along. </p>\n<p>Mama is feeding <a href=\"http://www.biobest.be/eng/EHeterorhabditis.htm\">nematodes</a> to her rhododendrons. Or is she feeding the rhodos to the nematodes?</p>\n<p>Today Christopher's cousins Lennart and Mareike bicyled to Lüllau to pay us a visit.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/05/crh0501.jpg\" height=\"300\" width=\"350\" border=\"0\" alt=\"crh0501.jpg: \" /></p>"},"next":{"fields":{"title":"Sunday","date":"30 Apr 2000","slug":"/2000-04-30-sunday/"}},"previous":{"fields":{"title":"Eskimo Roll","date":"03 May 2000","slug":"/2000-05-03-eskimo-roll/"}}},{"node":{"fields":{"title":"Sunday","date":"30 Apr 2000","slug":"/2000-04-30-sunday/","tags":"Y2000"},"html":"<p>I've started a tech diary at http://shanson.weblogs.com, but I doubt it will be of interest to anybody but me.</p>\n<p>It looks like edithispage and all the other Userland servers are back on the air after their move to Exodus.</p>"},"next":{"fields":{"title":"Father and Son Virus","date":"29 Apr 2000","slug":"/2000-04-29-father-and-son-virus/"}},"previous":{"fields":{"title":"May Day","date":"01 May 2000","slug":"/2000-05-01-may-day/"}}},{"node":{"fields":{"title":"Father and Son Virus","date":"29 Apr 2000","slug":"/2000-04-29-father-and-son-virus/","tags":"Y2000"},"html":"<p>We were both out a couple days with the flu. We're both better now (just in time for the long weekend). But now Mama has it.</p>\n<p>Saturday is bath day, when we try to take at least one picture of Christopher with funny hair.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/04/hair.jpg\" height=\"299\" width=\"350\" border=\"0\" alt=\"crh0429.jpg: \" /></p>"},"next":{"fields":{"title":"Wednesday","date":"26 Apr 2000","slug":"/2000-04-26-wednesday/"}},"previous":{"fields":{"title":"Sunday","date":"30 Apr 2000","slug":"/2000-04-30-sunday/"}}}],"pathPrefix":"","first":false,"last":false,"index":1065,"pageCount":1101}}

